<template>
  <div class="formatter">
        <div class="header">
            <div class="select-wrapper">
                Format
                <select id="language">
                    <option value="hql">
                        HQL
                    </option>
                    <option value="sql">
                        SQL
                    </option>
                    <option value="n1ql">
                        N1QL
                    </option>
                    <option value="db2">
                        DB2
                    </option>
                    <option value="pl/sql">
                        PL/SQL
                    </option>
                </select>
            </div>
        </div>
        <div class="main">
            <section class="input">
                <textarea id="input" autofocus="true" wrap="off">
                    select supplier_name, city from suppliers&#10;where supplier_id > 500&#10; ${ass} ORDER BY supplier_name ASC, city DESC;
                    </textarea>
            </section>
            <section class="output">
                <textarea id="output" readonly="true" wrap="off"></textarea>
            </section>
        </div>
  </div>
</template>

<script>
import sqlFormatter from "./components/sqlFormatter/index.js";

export default {
    data(){
        return { 
            formItem: {
                textarea: '',
            }
        }
    },
    mounted(){
        this.init()
    },

    methods: {
        init(){
            let language = document.getElementById('language');
            let input = document.getElementById('input');
            let output = document.getElementById('output');

            input.addEventListener('input', format);
            language.addEventListener('change', format);

            function format() {
                window.console.time('formatting');

                output.value = sqlFormatter.format(input.value, {language: language.options[language.selectedIndex].value, params: 'hahaha'});

                window.console.timeEnd('formatting');
            }
            format()
        }
    },


}
</script>

